<template>
  <div class="checkIndex">
    <div class="topHeader">
      <home system="2"></home>
    </div>

    <div class="index_box">
      <div class="topLogo">
        <!-- <div class="logofont">绿云国际大酒店</div> -->
        <div class="logofont">{{hotelName}}</div> 
        <!-- <div class="bc">{{authInfo.usercode}} - {{authInfo.cashier}}</div> -->
      </div>
      <router-link tag="div" to="/checkPackage" class="checkPackage">
        <div class="iconfont icon-goudui-"></div>
        <div class="checkPackageText">
          <p class="title">早餐核销</p>
          <p class="descript">支持刷房卡 / 手输房号</p>
        </div>
      </router-link>
      <div class="checkBox">
        <router-link tag="div" to="/checkStatistics" class="checkStatistics">
          <div class="iconfont icon-stat_icon"></div>

          <div class="checkStatisticsText">
            <p class="title">统计</p>
            <p class="descript">未用／已用</p>
          </div>
        </router-link>

        <router-link tag="div" to="/checkSet" class="checkSet">
          <div class="iconfont icon-shezhi"></div>
          <div class="checkSetText">
            <p class="title">设置</p>

          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast, Indicator, MessageBox } from "mint-ui";
import home from "../common/home";
export default {
  data() {
    return {
      hotelName: "",
      SYS_DEVICE: "",
      authInfo: {}
    };
  },
  mounted() {
    Indicator.close();
    this.SYS_DEVICE = this.$SYS_DEVICE;
     var gatewayJson = localStorage.getItem("simgateway");
      if (gatewayJson != null) {
        var gateway = JSON.parse(gatewayJson);
        this.hotelName = gateway.hotelName;
      } else {
        this.msg("未设置参数，请点设置进行设置");
      }
  },
  methods: {
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },

  components: {
    home
  }
};
</script>

<style lang="less">
.checkIndex {
  width: 100%; // border-top:0.0267rem solid #00487C;
  overflow: auto;
  height: 100vh;
  background: #003963;

  .index_box {
    width: 96%;
    height: auto;
    margin: 0 auto;

    .topLogo {
      margin-top: 0.2933rem;
      width: 100%;
      height: 2.6933rem;
      background: #00213f;
      border-radius: 0.1333rem;
      background-color: rgba(0, 0, 0, 0.3);
      text-align: center;

      .logofont {
        font-size: 0.75rem;
        color: #fff;
        line-height: 2.6933rem;
        height: 2.6933rem;
        overflow: hidden;
        width: 100%;
        word-break: keep-all;
        text-overflow: ellipsis;
      }

      .bc {
        color: #fff;

        i {
          font-size: 0.32rem;
          padding: 0.2rem 0.4rem;
        }
      }

      .red {
        color: red;
      }
    }

    .checkPackage {
      display: flex;
      justify-content: center;
      margin-top: 10px;
      align-items: center;
      height: 145px;
      border-radius: 5px;
      background-image: linear-gradient(-180deg, #fec004 3%, #f9c117 98%);

      .iconfont {
        font-size: 50px;
        color: #ffffff;
        margin-right: 37px;
      }

      .checkPackageText {
        color: #ffffff;

        .title {
          font-size: 28px;
          text-align: left;
        }

        .descript {
          font-size: 14px;
          text-align: left;
        }
      }
    }

    .checkBox {
      display: flex;
      justify-content: space-between;
      margin: 10px 0;

      .checkStatistics {
        flex-basis: 48.6%;
        height: 270px;
        background-image: linear-gradient(-270deg, #f44236 4%, #f75348 100%);
        border-radius: 5px;

        .iconfont {
          font-size: 50px;
          color: #ffffff;
          margin-top: 63px;
          text-align: center;
        }

        .checkStatisticsText {
          color: #ffffff;
          text-align: center;

          .title {
            font-size: 28px;
          }

          .descript {
            font-size: 14px;
          }
        }
      }

      .checkSet {
        flex-basis: 48.6%;
        height: 270px;
        background-image: linear-gradient(-180deg, #00bad4 0%, #14c3dc 98%);
        border-radius: 5px;

        .icon-shezhi {
          font-size: 50px;
          color: #ffffff;
          margin-top: 63px;
        }

        .checkSetText {
          color: #ffffff;
          text-align: center;

          .title {
            font-size: 28px;
          }

          .descript {
            font-size: 14px;
          }
        }
      }
    }
  }
}
</style>



